// flow weak

import React, {
  PureComponent
} from 'react';
import XDate from 'xdate';
import PropTypes from 'prop-types';
import {
  AnimatedView,
  StatsCard,
  EarningGraph,
  Notifications,
  WorkProgress,
  TwitterFeed,
  TodoListDemo,
  TeamMatesDemo
} from '../../components';

class Home extends PureComponent {

  componentWillMount() {
    const currentTime = new XDate().getTime();
    if (this.getTimeToLive() > currentTime) {
      const { actions: { enterHome } } = this.props;
      enterHome();
    } else {
      const { actions: { enterLogin } } = this.props;
      enterLogin();
      this.goLogin();
    }
  }

  getTimeToLive = () => {
    if (localStorage.getItem('cms@user') !== null) {
      const userAuth = JSON.parse(localStorage.getItem('cms@user'));
      return userAuth.created + userAuth.ttl;
    }
    return 0;
  }

  goLogin = () => {
    const { history } = this.props;
    history.push({ pathname: '/login' });
  }

  componentDidMount() {
    const {
      actions: {
        fetchEarningGraphDataIfNeeded,
        fetchTeamMatesDataIfNeeded
      }
    } = this.props;

    fetchEarningGraphDataIfNeeded();
    fetchTeamMatesDataIfNeeded();
  }

  componentWillUnmount() {
    const { actions: { leaveHome } } = this.props;
    leaveHome();
  }

  render() {
    const {
      teamMates,
      teamMatesIsFetching,
      earningGraphLabels,
      earningGraphDatasets
    } = this.props;

    return (
      <AnimatedView>
        <div
          className="row"
          style={{ marginBottom: '5px' }}>
          <div className="col-md-3">
            <StatsCard
              statValue={'3200'}
              statLabel={'Total Tasks'}
              icon={<i className="fa fa-check-square-o" />}
              backColor={'red'}
            />
          </div>
          <div className="col-md-3">
            <StatsCard
              statValue={'2200'}
              statLabel={'Total Messages'}
              icon={<i className="fa fa-envelope-o" />}
              backColor={'violet'}
            />
          </div>
          <div className="col-md-3">
            <StatsCard
              statValue={'100,320'}
              statLabel={'Total Profit'}
              icon={<i className="fa fa-dollar" />}
              backColor={'blue'}
            />
          </div>
          <div className="col-md-3">
            <StatsCard
              statValue={'4567'}
              statLabel={'Total Documents'}
              icon={<i className="fa fa-paperclip" />}
              backColor={'green'}
            />
          </div>
        </div>

        <div className="row">
          <div className="col-md-8">
            <EarningGraph
              labels={earningGraphLabels}
              datasets={earningGraphDatasets}
            />
          </div>
          <div className="col-lg-4">
            <Notifications />
          </div>
        </div>

        <div className="row">
          <div className="col-md-8">
            <WorkProgress />
          </div>
          <div className="col-md-4">
            <TwitterFeed />
          </div>
        </div>

        <div className="row">
          <div className="col-md-5">
            <TeamMatesDemo
              isFetching={teamMatesIsFetching}
              members={teamMates}
            />
          </div>
          <div className="col-md-7">
            <TodoListDemo />
          </div>
        </div>

      </AnimatedView>
    );
  }
}

Home.propTypes = {
  earningGraphLabels: PropTypes.array,
  earningGraphDatasets: PropTypes.array,
  teamMatesIsFetching: PropTypes.bool,
  history: PropTypes.object.isRequired,
  teamMates: PropTypes.arrayOf(
    PropTypes.shape({
      picture: PropTypes.string,
      firstname: PropTypes.string,
      lastname: PropTypes.string,
      profile: PropTypes.string,
      profileColor: PropTypes.oneOf(['danger', 'warning', 'info', 'success'])
    })
  ),
  actions: PropTypes.shape({
    enterHome: PropTypes.func,
    leaveHome: PropTypes.func,
    fetchEarningGraphDataIfNeeded: PropTypes.func,
    fetchTeamMatesDataIfNeeded: PropTypes.func
  })
};

export default Home;



